.chartcontainer {
    position: relative;
    height: 150vh;
    // width: 100vw;
    background: hsl(0, 0%, 91%);
    overflow: hidden;
    // display: flex;
    box-shadow: 2px 2px 8px var(--grayText);
    display: flex;
    margin: auto;
    padding: 1rem;
    max-width: 100vw;
    background-color:hsl(0, 0%, 18%);

    .chart {
        margin-left: 20px;
        width: 15rem;
        height: 33rem;
        // box-shadow: 1px 0px 4px var(--grayText);
   }
   .donut{
    box-shadow: 2px 2px 8px var(--grayText);
    border-radius: 1rem;
    position: absolute; 
    bottom: 1rem;
    right: 1rem; 
    z-index: 1000; 
    overflow: hidden;
    margin-left: 2rem;
    padding: 1rem;
    width: 30rem;
    max-width: 30rem;  
    display: flex;
    flex-direction: column; 
    align-items: center;

    .bin-selector{
        .drop-down {
            color: var( --whiteColor);
        }
    }

    .donutchart {
        margin-left: 1rem;
        display: flex;

    }

   }

   .upper-container {
        display: flex; 
        justify-content: space-between; 
        align-items: flex-start; 
    
        .linechart-container{
            height: 42rem;
            box-shadow: 1px 0px 4px var(--grayText);
            overflow: hidden;
            margin-left: 2rem;
            margin-top: 5rem;
            padding: 1rem;
            display: flex;
            flex-direction: column; 
            align-items: center;
            border-radius: 1rem;
        
            .linechart {
                width: 100vh;
                max-width: 100vw;
                display: flex;
            }
    
            .title1 {
                padding: 0;
                margin-bottom: 0rem;
            }
       }

       .realtime-card {
        margin-left: 2rem; 
        margin-top: 5rem; 
        display: flex;
        flex-direction: column; 
        max-width: 30%; 
    }
   }

  

  h3 {
    color: var(  --grayText);
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
    font-size: var( --h2fontSize);
  }
   
  .drop-down {
    color: var(--whiteColor);
    font-weight: 500;
    margin-bottom: 0.5rem;
}

#bin-select {
    background-color: #333; 
    color: white;
    border: 1px solid #555; 
    border-radius: 4px;
    padding: 0.5rem;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5); 
    appearance: none; 
    cursor: pointer;
}

#bin-select:hover {
    background-color: #444; 
}

#bin-select:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5); 
}
}

